<template>
  <Main class="">
    <header class="blog-header">
      <ul>
        <li class="item">
          <router-link to="/home">Home</router-link>
        </li>
        <li class="item">
          <router-link to="/blog">Blog</router-link>
        </li>
        <li class="item">
          <a href="http://129.204.39.187">Cinema</a>
        </li>
      </ul>
    </header>
    <Main class="blog-con">
      <section class="blog-con-left">
        <div class="blog-con-left-header">
          <span class="head"></span>
          <p style="margin: 5px;color: #098e8e">只要有斗志，不怕没战场。</p>
          <p style="color: #071eee;font-family: -webkit-pictograph;text-shadow: 0 0 10px #555">As long as there is fighting spirit, we are not afraid of no battlefield.</p>
        </div>
        <div class="blog-con-left-con">
          <el-tag v-for="item in tagclass" :type="rgb[item['color']]">{{item['name']}}</el-tag>
        </div>
      </section>
      <section class="blog-con-right">
        <el-carousel :interval="4000" type="card" height="200px">
          <!--<el-carousel-item v-for="item in carousel" :key="item">-->
            <!--<img :src="item.src"/>-->
            <!--<h3>{{ item.title }}</h3>-->
          <!--</el-carousel-item>-->
          <el-carousel-item>
            <img src="./../assets/images/carousel/1.jpg"/>
            <h3>nodejs爬虫</h3>
          </el-carousel-item>
          <el-carousel-item>
            <img src="./../assets/images/carousel/2.jpg"/>
            <h3>js运行机制</h3>
          </el-carousel-item>
          <el-carousel-item>
            <img src="./../assets/images/carousel/3.jpg"/>
            <h3>浏览器渲染机制</h3>
          </el-carousel-item>
        </el-carousel>
        <ul class="blog-con-right-con">
          <blog-item v-for="item in carousel" :item="item"></blog-item>
        </ul>
      </section>
      <div style="clear: both;"></div>
    </Main>
    <footer class="blog-footer">
      <span>vue @2.5.2</span>
      <span>elementUi @2.6.1</span>
    </footer>
  </Main>
</template>

<script>
import BlogItem from '@/components/BlogItem.vue'
export default {
  name: 'blog',
  components:{BlogItem},
  data () {
    return {
      rgb:['', 'success', 'info', 'warning', 'danger'],
      carousel:[
        {title: 'nodejs爬虫', src: './../assets/images/carousel/1.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {title: 'js运行机制', src: './../assets/images/carousel/2.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {title: '浏览器渲染机制', src: './../assets/images/carousel/3.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {title: 'nodejs爬虫', src: './../assets/images/carousel/1.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {title: 'js运行机制', src: './../assets/images/carousel/2.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'}
      ],
      tagclass:[
        {name:'html', color:0, count:8}, {name:'css', color:1, count:8}, {name:'js', color:2, count:8}, {name:'vue', color:3, count:8}, {name:'nodejs', color:4, count:8},
        {name:'php', color:0, count:8}, {name:'Asp.net', color:1, count:8}, {name:'sqlserver', color:2, count:8}, {name:'mysql', color:3, count:8}, {name:'Android', color:4, count:8},
        {name:'elementui', color:0, count:8}, {name:'mintui', color:1, count:8}, {name:'python', color:2, count:8}, {name:'java', color:3, count:8}, {name:'smail', color:4, count:8}
      ]
    }
  }
}
</script>

<style scoped lang="less">
  .blog-header, .blog-footer {
    background-color: #00BCD4;
    color: #f7f7f7;
    text-align: center;
    line-height: 60px;
    opacity: 0.7;
  }
  .blog-header ul{
    overflow: hidden;
    margin: 0 auto;
  }
  .blog-header .item{
    min-width: 60px;
    float: left;
    list-style: none;
  }
  .blog-con{
    margin: 10px auto 10px;
    min-height: 602px;
    background-color: #333;
    opacity: 0.8;
  }
  .blog-con-left,.blog-con-right{
    float: left;
    min-height: 800px;
  }
  .blog-con-left{
    width: 35%;
    /*min-height: 500px;*/
    box-shadow: 0 0px 10px 0 #ccc;
  }
  .blog-con-left-header{
    height: 200px;
    overflow: hidden;
    background: #05192e;
  }
  .blog-con-left-header .head{
    height: 80px;
    width: 80px;
    margin-top: 25px;
    display: inline-block;
    border-radius: 50%;
    background-color: #222222;
    background-image: url("./../assets/images/head.jpeg");
    background-position: 50% 50%;
    background-size: 100%;
  }
  .blog-con-left-con{
    margin-top: 20px;
  }
  .blog-con-right{
    margin-left: 1%;
    width: 63%;
  }
  .blog-footer span{
    margin: 4px;
  }
  .el-carousel__item h3 {
    width: 100%;
    position: absolute;
    bottom: 10px;
    font-size: 16px;
    color: #fff;
    -webkit-text-shadow: 0 0 10px #333;
    -moz-text-shadow: 0 0 10px #333;
    text-shadow: 0 0 10px #333;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .el-tag{
    margin: 4px;
    cursor: pointer;
  }
  @media screen and (min-width: 1400px){
    .blog-con,.blog-header ul{
      width: 1200px;
    }
  }
  @media screen and (min-width: 1000px) and (max-width: 1400px){
    .blog-con,.blog-header ul{
      width: 800px;
    }
  }
  @media screen and (max-width: 600px){
    .blog-con,.blog-header ul{
      width: 100%;
    }
    .blog-con-left{
      display: none;
    }
    .blog-con-right{
      width: 100%;
      margin-left: 0;
      padding: 0 1px;
    }
    .el-carousel__item--card.is-active{
      width: 100% !important;
      transform: translateX(0) scale(1) !important;
    }
    .el-carousel__item:nth-child(n) {
      /*width: 0;*/
    }
  }
</style>



